.accent-btn {
    text-decoration: none;
    display: inline-block;
    box-shadow: 0px 4px 0px rgba(22, 22, 25, 0.75);
    border-radius: 8px;
    padding: 20px 10px;
    text-align: center;
    margin-top: 35px;
    font-family: $mainFont;
    font-weight: 700;
    font-size: 1.93rem;
    color: #fff;
    text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.75);
    transition: background 0.35s;
    width: 100%;
    border: none;
    margin-bottom: 5px;
    cursor: pointer;
    outline: none;

    &.alert {
        background: #DA2745;

        &:hover {
            background: lighten(#DA2745, 10%)
        }
    }

    &.import {
        background: $accentColor2;

        &:hover {
            background: lighten($accentColor2, 10%)
        }
    }

    &.big {
        max-width: 456px;

        @include md {
            max-width: none;
        }
    }
    &.mid {
        max-width: 320px;
    }

    @include md {
        font-size: 1.5rem;
        padding: 15px 5px;
	}
}