.form-lay {
  background-color: #fff;
  font-family: $mainFont;
  font-size: 16px;
  font-weight: 500;

  .title {
    text-align: left;
    margin-bottom: 15px;
  }

  .divider {
    display: block;
    width: 100%;
    position: relative;
    // margin: 15px 0;

    &:before {
      content: '';
      width: 100%;
      height: 1px;
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      z-index: 1;
      background: #535353;
      opacity: .3;

    }

    p {
      display: inline-block;
      padding: 0 10px;
      background: #fff;
      position: relative;
      z-index: 2;
      color: #535353;
    }
  }

  .shipping-info {
    text-align: left;
    padding: 10px 0 10px 60px;
    position: relative;
    margin: 15px 0;
    font-weight: 700;
    color: #535353;

    &:before {
      content: '';
      width: 40px;
      height: 40px;
      position: absolute;
      left: 0;
      top: 25%;
      transform: translateY(-50%);
      display: block;
      background: url(../img/location.svg) center no-repeat;
      background-size: contain;
    }

    svg {
      fill: #535353;
    }
  } 


  .row {
    display: flex;
    justify-content: space-between;
    margin: 0 0 10px;
  }

  .field {
    position: relative;
    margin: 10px 0 0;
    width: 100%;
    height: 50px;
    // margin: 0 10px;
    border: 1px solid #cececf;
    border-radius: 4px;
    margin-left: 10px;

    &.fail {
      border-color: red;
    }
  
    &:first-child {
      margin-left: 0;
    }

    .half-width {
      width: 50%;
    }

    .quarter-width {
      width: calc(25% - 10px);
    }

    label {
      position: absolute;
      width: 100%;
      left: 13px;
      bottom: 13px;
      color: transparent;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      transform-origin: 0 50%;
      text-align: left;
      cursor: text;
      transition-property: color, transform;
      transition-duration: 0.3s;
      transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    .input {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 4px;
      left: 0;
      bottom: 0;
      padding: 13px 13px 11px;
      color: #535353;
      background-color: transparent;
      border: none;

      &:focus ~ label {
        transform: translateY(-35px);
        color: #535353;
      }
    }

  }
}


