.payment-section {
  // margin: 150px;
}

.form-group {
  padding: 10px 40px;
  margin: 100px 0 0;
  background: #EFEFEF;

  @include sm {
    padding: 0 20px;
  }
}

.pay-info {
  text-align: center;
  display: inline-block;
  padding: 10px 0 10px 60px;
  position: relative;
  margin: 15px 0 75px;
  font-weight: 700;
  color: #535353;

  &:before {
    content: '';
    width: 40px;
    height: 40px;
    position: absolute;
    left: 0;
    top: 35%;
    transform: translateY(-50%);
    display: block;
    background: url(../img/card.svg) center no-repeat;
    background-size: contain;
  }

  svg {
    fill: #535353;
  }
} 

#pay-trigers {
  text-align: center;

  .zoid-outlet {
    max-width: 400px !important;
    text-align: center;
    width: 100% !important;
    // margin: 0 auto !important;
  }
}

