// footer
.main-footer {
	& > div {
		position: relative;
		z-index: 4;
	}
	
	.text {
		margin: 0;
	}
}


.main-footer__top-row {
	background: #da2745;
	
	position: relative;
	overflow: hidden;
	text-align: center;

	.top-row__inner {
		position: relative;
		padding: 20px 40px;
		
		&:before {
			content: '';
			position: absolute;
			width: calc(100% + 30px);
			height: calc(100% + 30px);
			left: -15px;
			top: 0;
			z-index: -1;
			box-shadow: inset 0 0 15px rgba(22, 22, 25, 0.8);
		}
	}

	.top-row__faq-lay {
		margin: 0;
		background: #fff;
		max-height: 0;
		transition: max-height 0.3s ease-in-out;

		&.opened {
			max-height: 1500px;
		}
	}

		.faq-list {
			text-align: left;
			padding: 0;
			list-style: none;
			margin: 0;
		}
			.faq-item {
				padding: 15px;
				overflow: hidden;
			}
				.faq-quest {
					border-top-right-radius: 8px;
					border-top-left-radius: 8px;
					padding: 15px;
					background: gray;
					color: #fff;
				}
				.faq-answer {
					padding: 15px;
					border: 1px solid gray;
					border-bottom-right-radius: 15px;
					border-bottom-left-radius: 15px;
				}

	
	
	.main-footer__link {
		color: #fff;
		cursor: pointer;
	}
}

.main-footer__bottom-row {
	padding: 40px;
	background: $darkBg;

	.main-footer__disclamer {
		color: $titleGrayColor;
		font-size: 0.75rem;
		text-align: center;
	}

	.main-footer__main-nav-list {
		list-style: none;
		margin: 25px 0 0;
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;

		@include sm {
			flex-direction: column;
		}
	}
		.main-footer__main-nav-item {
			&:not(:first-child) {
				margin-left: 75px;
				
				@include sm {
					margin: 10px 0 0 0;
				}
			}


		}
			.main-footer__main-nav-link {
				color: #919b9a;
				text-decoration: none;
				cursor: pointer;

				&:hover {
					color: #fff;
				}
			}
}


.modal-content {

	.title {
		margin-left: 0;
	}
