* {
	box-sizing: border-box;
}
html {
	width: 100%;
	height: 100%;
	overflow-x: hidden;
}
body {
	font-family: $mainFont;
	font-size: 16px;
	width: 100%;
	// height: 100%;
	background: $darkBg;
	// position: fixed;
	
	// align-items: center;
	// justify-content: center;
}

h1, h2, h3, h4, h5, h6 {
	padding: 0;
	margin: 0;
}

.hide-mob {
	display: none;

	@include md {
		display: initial;
	}

}

.hide-mob {
	@include md {
		display: none;
	}
}

.wrapper {
	background: #fff;
	max-width: 1200px;
	min-height: 100vh;
	width: 100%;
	margin: 0 auto;
	position: relative;
	display: flex;
	flex-direction: column;

	@include xl {
		max-width: 997px; 
	}

	@include lg {
	
	}

	@include md {
		max-width: 100%; 
	}
	
	@include sm {
		width: 100%; 
	}
}

.container {
	// max-width: 1200px;
	// width: 100%;
	// margin: 0 auto;
	padding: 0 30px;	
	// position: relative;

	// @include xl {
	// 	max-width: 997px; 
	// }

	// @include lg {
	
	// }

	// @include md {
	// 	max-width: 100%; 
	// }
	
	@include sm {
		width: 100%; 
		padding: 0 15px;
	}
}

.img-responsive {
	max-width: 500px;
	width: 100%;
	height: auto;
}


.title {
	font-weight: 700;
	font-size: 2.25rem;
	margin: 25px auto 0;
	max-width: 1000px;

	@include xl {
		font-size: 2rem;
	}

	@include sm {
		font-size:  1.5rem;
	}
}
.sub-title {
	font-weight: 400;
	font-size: 1.5rem;
	margin: 15px auto 0;
	max-width: 1000px;

	@include xl {
		font-size: 1.2rem;
	}

	@include sm {
		font-size: 1rem;
	}
}

.text {
	font-size: 1rem;
	margin: 25px 0;
	font-weight: 400;
	line-height: 1.5;
	// letter-spacing: 1px;

	&:first-child {
		margin-top: 0;
	}

	@include xl {
		font-size: 0.875rem; 
		margin: 15px 0;
	}

	// @include lg {
	// 	font-size: 0.8125rem; 
	// 	margin: 10px 0;
	// }
}

.bold {
	font-weight: 700;
}

.accented {
	color: $accentColor;
}
.gray {
	color: $titleGrayColor;
}

