.main {
	text-align: center;
	flex-grow: 1;
	margin: 0;
	background: #fff;
}

// top section
.top-section{

}

.top-section__dscr-wrap {
	display: flex;
	padding: 75px 0;

	@include md {
		flex-direction: column;
		padding: 35px 0;
	}
}

.top-section__gallery-col,
.top-section__dscr-col {
	width: 50%;

	@include md {
		width: 100%;
	}
}


.top-section__gallery-col {

}

.gallery-col__video-container {
	position: relative;
	padding-bottom: 56.25%;
	display: none;

	iframe {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}

.gallery-col__media-container {
	height: 0;
    padding-bottom: 72%;
	overflow: hidden;
}

.gallery-col__main-img {
	display: block;
	max-width: 100%;
	height: auto;
	margin: 0 auto;
}

.gallery-col__prev-list {
	list-style: none;
	padding: 0;
	margin: 20px 0 0;
	display: flex;
	flex-wrap: wrap;
}

.gallery-col__prev-item {
	width: calc(100%/5);
	cursor: pointer;

	@include sm {
		width: calc(100%/4);
	}
}

.gallery-col__prev-img {
	max-width: 100%;
	display: block;
	height: auto;
}

.top-section__dscr-col {
	text-align: left;
	padding-left: 50px;	

	@include xl {
		padding-left: 20px;
	}

	@include md {
		padding-left: 0;
		margin-top: 50px;
	}
}

// howtouse section
.howtouse-section {
	background: $accentBg;
	padding: 35px 0;

	.title,
	.sub-title {
		max-width: 600px;
	}
}

.howtouse-section__steps-list {
	list-style: none;
	padding: 0;
	display: flex;
	margin: 58px 0 0 -58px;

	@include md {
		flex-direction: column;
		margin: 20px 0 0;
	}
}

.howtouse-section__steps-item {
	flex-grow: 1;
	width: 50%;
	margin-left: 58px;
	text-align: left;

	@include md {
		width: 100%;
		margin: 15px 0 0;
	}
}

.howtouse-section__aftertext {
	margin: 0;
}

// feddback section

.feedback-section {
	padding: 0 0 75px;
}

// products section

.product-section {
	padding: 35px 0;
	background: $accentBg;
	border-bottom: 1px solid gray;
}

.product-section__product-list {
	list-style: none;
	padding: 0;
	margin: 0;
	display: flex;

	@include lg {
		flex-direction: column;
	 }

	 @include sm {
		margin: 0 -10px 0;
	}
}

.product-section__product-item {
	padding: 20px 23px 37px;
	background: #fff;
	border-radius: 8px;
	width: calc(100%/3);
	position: relative;
	box-shadow: 0px 0px 15px rgba(22, 22, 25, 0.25);
	overflow: hidden;
	display: flex;
	flex-direction: column;

	@include lg {
		width: 100%;
		margin-top: 10px;
	 }

	 @include md {
		padding: 10px;
	}
	 @include sm {
		padding: 4px;
	}

	&.accented-item {
		z-index: 1;
		border: 3px solid $accentColor;

		&:before {
			content: 'Best Deal';
			position: absolute;
			height: 35px;
			width: 150px;
			right: -35px;
			top: 20px;
			transform: rotate(45deg);
			background: $accentColor2;

			line-height: 35px;
			font-weight: 700;
			font-size: 1rem;
			color: #fff;
		}
	}

	

	.text {
		margin: 0;

		&.old-price {
			color: red;
			text-decoration: line-through;
		}
		&.new-price {
			color: green;
		}

		&.m-spacer {
			margin: 20px 0 0;
		}

		&:last-of-type {
			margin-bottom: 35px;
		}

		&.big {
			font-size: 1.5rem;

			@include md {
				font-size: 0.875rem;
			}
		}
	}

	.accent-btn {
		margin-top: auto;
	}
}

.product-item__link {
	text-decoration: none;
	height: 100%;
	color: #000;
}

.product-item__flex {
	display: flex;
	flex-direction: column;
	height: 100%;
	align-items: center;

	@include lg {
		flex-direction: row;
		margin-top: 20px;
	 }
}

.product-item__checkbox-container {
	align-self: center;
	display: none;
	font-size: 0;
	min-height: 24px;
	position: relative; 

	@include md {
		display: block;
	 }


	input[type=checkbox] {
		position: relative;
		outline: none;
		width: 24px;
		height: 24px;
		border: 2px solid #ced8dd;
		background: #f2f9fd;
		pointer-events: none;
	}
}

.product-item__left {
	@include lg {
		max-width: 200px;
		padding: 0 10px;
	 }

	 img {
		display: block;
		max-width: 100%;
		margin: 20px auto 0;

		@include lg {
			margin: 0 auto 0;
		 }
	}

	@include md {
		max-width: 90px;
	}
}
.product-item__right {
	display: flex;
	flex-direction: column;
	height: 100%;

	@include lg {
		flex-grow: 1;
		padding-left: 70px;
		text-align: left;

		.text {
			margin-top: 0 !important;
		}

		.accent-btn {
			margin-top: 50px;
		}
	 }

	 @include md {
		 padding-left: 20px;

		 .accent-btn {
			 display: none;
		 }
	 }
}

