//- guarantee section

.guarantee-section {
	// background: $accentBg;
	padding: 35px 0;
	

	.text {
		margin: 30px 0 0;
	}
}

.guarantee-section__company-list {
	list-style: none;
	margin: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	padding: 0;
}

.guarantee-section__company-item {
	width: 45.75px;
	height: 29.01px;
	margin-left: 3px;

	&:nth-child(1) {
		background: url(../img/payment.svg) 71.42857142857143% 11.104731281580156% no-repeat;
		background-size: 366px 290.25px;
		margin-left: 0;
	}
	&:nth-child(2) {
		background: url(../img/payment.svg) 0 22.20946256316031% no-repeat;
		background-size: 366px 290.25px;
	}
	&:nth-child(3) {
		background: url(../img/payment.svg) 42.857142857142854% 0 no-repeat;
		background-size: 366px 290.25px;
	}
	&:nth-child(4) {
		background: url(../img/payment.svg) 28.571428571428573% 44.41892512632062% no-repeat;
		background-size: 366px 290.25px;
	}
	&:nth-child(5) {
		background: url(../img/payment.svg) 100% 26.00429% no-repeat;
		background-size: 732px 580.5px;
		width: 91.5px;
		height: 29px;
	}
	&:nth-child(6) {
		background: url(../img/shopify.png) center center no-repeat;
		background-size: contain;
		width: 90px;
		height: 35px;
	}
}

// s offer section
.s-offer-section {
	// padding: 35px 0;
	overflow: hidden;
	max-height: 0;
	transition: max-height 0.5s ease-in-out;

	&.opened {
		max-height: 1500px;
	}
}

.s-offer-section__dscr-wrap {
	display: flex;
	margin: 35px 0;

	@include md {
		flex-direction: column;
	 }
}

.s-offer-section__img-col,
.s-offer-section__dscr-col {
	width: 50%;

	@include md {
		width: 100%;
	 }

	.text {
		margin: 0;

		&.m-spacer {
			margin: 20px 0 0;
		}

		.import {
			color: $accentColor2;
			font-size: 1.25rem;
		}
	}
}

.s-offer-section__img-col {
	img {
		display: block;
		max-width: 100%;
		height: auto;
		margin: 0 auto;
	}
}

.s-offer-section__dscr-col {
	text-align: left;
	padding-left: 50px;

	@include md {
		padding-left: 0;
		margin-top: 30px;
	 }
}

.s-offer-section__no-s-offer-link {
	display: block;
	text-align: center;
	margin: 35px 0 0;
	font-weight: 700;
	color: $textColor;
	text-decoration: none;
}


// address

.form-section {
	overflow: hidden;
	max-height: 0;
	transition: max-height 0.5s ease-in-out;

	&.opened {
		max-height: 1500px;
	}

	.container {
		padding: 0;
	}
}

.form-section__wrap {
	display: flex;
	margin-top: 50px;

	@include lg {
		flex-direction: column;
	 }
}

.form-section__cart-list-container {
	width: 50%;
	padding: 40px;
	background: #EFEFEF;
	display: flex;
	flex-direction: column;
	// border-left: 1px solid #bcb5b9;

	@include lg {
		width: 100%;
		border: none;
		background: transparent;
		order: -1;
	 }
	@include sm {
		padding: 20px;	
	 }
}

	.cart-list {
		list-style: none;
		margin: 0;
		// height: 100%;
		padding: 0;
	} 
		.cart-item {
			display: flex;
			align-items: center;
			
			&:not(:first-child) {
				margin-top: 15px;
			}
			.text {
				margin: 0;
			}
		}
			.cart-item__img-container {
				width: 80px;
				min-width: 80px;
				font-size: 0;
				border-radius: 4px;
				overflow: hidden;
				img {
					max-width: 100%;
				}
			}

			.cart-item__dscr-container {
				flex-grow: 1;
				padding-left: 10px;
				text-align: left;
			}

			.cart-item__price-container {
				padding-left: 20px;
				min-width: 80px;
			}

	.total-block {
		margin-top: auto;
	}
		.total-block__style-line {
			width: 100%;
			border-top: 1px dashed #cececf;
			margin: 15px 0;
		}

		.total-block__price-row {
			width: 100%;
			display: flex;
			margin: 20px 0;

			.total-block__name {
				margin-right: auto;
			}
			.total-block__price {
				&.large {
					font-size: 1.5rem;
					font-weight: 700;
				}
			}
			
		}


		
.form-section__payform-container {
	// min-width: 350px;
	padding: 0 40px;
	width: 50%;

	@include lg {
		width: 100%;
	 }

	 @include sm{
		padding: 20px;
	 }
}

// payment

.payment-section {
	overflow: hidden;
	max-height: 0; 
	transition: max-height 0.5s ease-in-out;

	&.opened {
		max-height: 1500px;
	}

	.container {
		padding: 0;
	}
}


