.feedback__list {
    list-style: none;
    margin: 40px 0 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feedback {
    display: flex;
    margin: 75px 0 0;
    max-width: 900px;
    width: 100%;

    &:first-child {
        margin: 0 0 0;
    }

    &:nth-child(2n) {

        .feedback__main-col {
            order: -1;
            padding: 0 50px 0 0;

            @include sm {
                order: 0;
             }
        }

    }

    @include sm {
        flex-direction: column;
        margin: 0;
        padding: 25px 0;
        border-top: 1px dashed gray;

        &:first-child {
            border-top: none;
        }
	}
}

.feedback__sliders-col {
    min-width: 200px;
    width: 20%;

    @include sm {
       width: 100%;
     }
}

