.feedback__stars {
    width: 100%;
    font-size: 0;
    svg {
        width: 20%;
        max-width: 20px;
    }

    @include sm {
        max-width: 100px;
     }
}

.feedback__name {
    margin-top: 16px;
    font-weight: bold;

    @include sm {
        text-align: left;
     }
}

.feedback__main-col {
    // flex-grow: 1;
    width: 80%;
    padding: 0 0 0 50px;
    text-align: left;

    @include xl {
		padding: 0 0 0 30px; 
    }
    
    @include sm {
        padding: 0 !important;
        margin-top: 10px;
        width: 100%;
     }
}

.feedback__title {
    // font-family: $mainFont;
    font-weight: 700;
    font-size: 1.187rem;
    line-height: 1.7;
    margin-top: 20px;
    // color: $titleGrayColor;
}

.feedback__text {
    // font-family: $mainFont;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.5;
    color: $textColor;

    @include xl {
		font-size: 0.875rem; 
	}
}

